/**
 * Auth Service API Client
 * Handles all authentication-related API calls with secure token management
 */

const BASE_URL = process.env.NEXT_PUBLIC_AUTH_SERVICE_URL || "http://127.0.0.1:8000/api"

export interface LoginRequest {
  email: string
  password: string
}

export interface LoginResponse {
  access: string
  refresh: string
}

export interface OTPRequest {
  email: string
  purpose: string
}

export interface OTPVerifyRequest {
  email: string
  otp: string
  new_password: string
}

export interface RefreshTokenRequest {
  refresh: string
}

export interface RefreshTokenResponse {
  access: string
}

export interface LogoutRequest {
  refresh: string
}

export interface TeamDetails {
  id: string
  name: string
  institution: string
  poc: any
  faculty_mentor: {
    id: string
    name: string
  }
  problem_statement: {
    id: string
    title: string
  }
  members: Array<{
    id: string
    role: string
    user: {
      id: string
      email: string
      name: string
    }
  }>
}

export interface POCTeam {
  id: string
  name: string
  institution: string
  [key: string]: any
}

class AuthService {
  private accessTokenKey = process.env.NEXT_PUBLIC_ACCESS_TOKEN_KEY || "sih_access_token"
  private refreshTokenKey = process.env.NEXT_PUBLIC_REFRESH_TOKEN_KEY || "sih_refresh_token"

  /**
   * Store tokens securely in localStorage
   */
  private setTokens(access: string, refresh: string) {
    if (typeof window !== "undefined") {
      localStorage.setItem(this.accessTokenKey, access)
      localStorage.setItem(this.refreshTokenKey, refresh)
    }
  }

  /**
   * Get access token
   */
  getAccessToken(): string | null {
    if (typeof window !== "undefined") {
      return localStorage.getItem(this.accessTokenKey)
    }
    return null
  }

  /**
   * Get refresh token
   */
  getRefreshToken(): string | null {
    if (typeof window !== "undefined") {
      return localStorage.getItem(this.refreshTokenKey)
    }
    return null
  }

  /**
   * Clear all tokens (logout)
   */
  clearTokens() {
    if (typeof window !== "undefined") {
      localStorage.removeItem(this.accessTokenKey)
      localStorage.removeItem(this.refreshTokenKey)
    }
  }

  /**
   * Make API request with automatic token refresh on 401
   */
  private async request<T>(endpoint: string, options: RequestInit = {}, retryCount = 0): Promise<T> {
    const url = `${BASE_URL}${endpoint}`
    const headers = new Headers(options.headers || {})

    // Add auth token if available
    const accessToken = this.getAccessToken()
    if (accessToken) {
      headers.set("Authorization", `Bearer ${accessToken}`)
    }

    const response = await fetch(url, {
      ...options,
      headers,
    })

    // Handle 401 - try to refresh token
    if (response.status === 401 && retryCount === 0) {
      const refreshToken = this.getRefreshToken()
      if (refreshToken) {
        try {
          const newAccessToken = await this.refreshAccessToken(refreshToken)
          // Retry original request with new token
          return this.request<T>(endpoint, options, 1)
        } catch (error) {
          // Refresh failed, clear tokens and redirect to login
          this.clearTokens()
          if (typeof window !== "undefined") {
            window.location.href = "/login"
          }
          throw new Error("Session expired. Please login again.")
        }
      }
    }

    if (!response.ok) {
      const error = await response.json().catch(() => ({}))
      throw new Error(error.detail || error.message || `API Error: ${response.status}`)
    }

    return response.json()
  }

  /**
   * Login with email and password
   */
  async login(request: LoginRequest): Promise<LoginResponse> {
    const response = await this.request<LoginResponse>("/token/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(request),
    })

    this.setTokens(response.access, response.refresh)
    return response
  }

  /**
   * Refresh access token
   */
  private async refreshAccessToken(refreshToken: string): Promise<string> {
    const response = await this.request<RefreshTokenResponse>("/token/refresh/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ refresh: refreshToken }),
    })

    const accessToken = response.access
    localStorage.setItem(this.accessTokenKey, accessToken)
    return accessToken
  }

  /**
   * Request OTP for first login
   */
  async requestOTP(email: string): Promise<{ detail: string }> {
    return this.request("/auth/send-otp/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, purpose: "first_login" }),
    })
  }

  /**
   * Verify OTP and set new password
   */
  async verifyOTPAndSetPassword(request: OTPVerifyRequest): Promise<{ detail: string; next: string }> {
    return this.request("/auth/verify-otp/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(request),
    })
  }

  /**
   * Get team details for Team Leader
   */
  async getTeamDetails(): Promise<TeamDetails> {
    return this.request("/leader/team/")
  }

  /**
   * Confirm team details (one-time, after first login)
   */
  async confirmTeamDetails(): Promise<{ detail: string; next: string }> {
    return this.request("/leader/team/confirm/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({}),
    })
  }

  /**
   * Get teams assigned to POC
   */
  async getPOCTeams(): Promise<POCTeam[]> {
    return this.request("/poc/teams/")
  }

  /**
   * Get POC team detail
   */
  async getPOCTeamDetail(teamId: string): Promise<TeamDetails> {
    return this.request(`/poc/teams/${teamId}/`)
  }

  /**
   * Logout single session
   */
  async logout(): Promise<{ detail: string }> {
    const refreshToken = this.getRefreshToken()
    if (!refreshToken) {
      this.clearTokens()
      return { detail: "Logged out" }
    }

    try {
      const response = await this.request<{ detail: string }>("/auth/logout/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ refresh: refreshToken }),
      })
      this.clearTokens()
      return response
    } catch (error) {
      // Even if API call fails, clear local tokens
      this.clearTokens()
      return { detail: "Logged out" }
    }
  }

  /**
   * Logout all sessions
   */
  async logoutAll(): Promise<{ detail: string }> {
    try {
      const response = await this.request<{ detail: string }>("/auth/logout_all/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
      })
      this.clearTokens()
      return response
    } catch (error) {
      this.clearTokens()
      return { detail: "Logged out from all devices" }
    }
  }
}

export const authService = new AuthService()
